.benefits__list__item {
  position: relative;
  width: 300px;
  border-radius: 20px;
  overflow: hidden;
  color: white;
}

.benefits__list__item__image__container {
  width: 100%;
  height: 100%;
}

.benefits__list__item__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.benefits__list__item__title {
  position: absolute;
  width: 100%;
  height: 70px;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5.5px);
  left: 0;
  bottom: 0;
  padding: 15px 30px;
  display: flex;
  align-items: center;
}
